.App {
	background-color: #f0f0ff;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	overflow: hidden;
}

.timelineContainer {
	width: 100%;
	height: 100%;
	overflow: hidden;
	user-select: none;
}

.yearMarker {
	position: absolute;
	top: 0;
	height: 100%;
	border-left: 1px dotted #aaa;
}

.todayMarker {
	position: absolute;
	top: 0;
	height: 100%;
	border-left: 1px solid #f00;
}

.yearLabel {
	position: absolute;
	bottom: 10px;
	left: -4px;
	padding: 0;
	font: 12px sans-serif;
	user-select: none;
	transform: rotate(-90deg);
}

.eventCollectionBar {
	position: absolute;
	top: 0;
	left: 0;
	height: 30px;
	width: 100%;
	background-color: #aaa;
	opacity: 0.5;
}

.eventCollectionContainer {
	overflow: clip;
}

.eventCollectionTitle {
	position: absolute;
	top: 0;
	left: 5px;
	height: 30px;
	font: 24px Georgia;
	font-variant-caps: all-small-caps;
	color: #444;
	font-weight: bold;
	width: 100%;
	text-align: left;
	user-select: none;
}

.eventsContainer {
	position: absolute;
	top: 40px;
	left: 0;
}

.eventBar {
	position: absolute;
	left: 0;
	top: 0;
}

.eventDot {
	position: absolute;
	left: 0;
	top: 0;
	border-radius: 50%;
	cursor: pointer;
	opacity: 0.5;
}

.eventTitle {
	user-select: none;
	cursor: default;
	position: absolute;
	top: 0;
	text-shadow: '0 0 5px white';
}

.controls {
	position: absolute;
	display: flex;
	flex-direction: row;
	top: 0px;
	right: 10px;
	padding: 4px;
}

.refreshButton {
	font: 12px sans-serif;
	cursor: pointer;
	user-select: none;
	margin-left: 5px;
}
